import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/page/Login'
import Home from '@/page/home'
import Daikuan from '@/page/daikuan'
import Baoxian from '@/page/baoxian'
import Zhuandan from '@/page/zhuandan'
import First from '@/page/first'
import Text from '@/page/text'
Vue.use(Router)
export default new Router({
    routes: [{
            path: '/',
            redirect: '/Login'
        },
        {
            path: '/Login',
            component: Login
        },
        {
            path: '/text',
            component: Text
        },
        {
            path: '/Home',
            name: ' Home',
            component: Home,
            children: [{
                path: '/Home/daikuan',
                component: Daikuan
            }, {
                path: '/Home/zhuandan',
                component: Zhuandan
            }, {
                path: '/Home/baoxian',
                component: Baoxian
            }, {
                path: '/Home/first',
                component: First
            }]
        },
    ]
})


