class Watch {
    constructor() {
        this._events = {};
    }
    on(type, watcher) {
        if (!(this._events[type] instanceof Array)) {
            this._events[type] = [];
        }
        this._events[type].push(watcher);
    }
    emit(type, ...reset) {
        if (!(this._events[type] instanceof Array)) {
            return;
        }
        this._events[type].forEach(item => {
            item(reset);
        });
    }
}
export default new Watch();


