<template>
  <div class="hello" ref="big"  @mouseenter="mouseEnter" @mouseleave="mouseLeave">
   <div class="wrapper" ref="wrap" :style="{top:siderY+'px'}">
   <Collipse v-for="(it,index) in sj" :key="index" @handleCollipse="handchange"  :index="index">
    <div slot="head" :class="sj[index].title==bkd?'choose hd':'hd'"> 
    <router-link :to="{path:it.path}">{{it.title}}</router-link> 
    </div>
    <div v-if="it.cout?true:false"  slot="list" v-for="(val,ind) in it.cout" :key="ind"  @click="add(val.name,val.path)" :class="flag==val.name?'small active':'small'">
     <router-link :to="{path:val.path}">{{val.name}}</router-link>
    </div>
   </Collipse>
   </div>
   <div class="scrolly" ref="gun" v-show="isShow">
   <div class="move" :style="{height:moveTo+'px',top:positionLT+'px'}" ref="mov" @mousedown="down"></div>
   </div>
   </div>
</template>

<script>
function mousemove (ev){
    let y = ev.clientY - this.my;
    if(y<0){
      y=0;
    }else if(y>=this.$refs.gun.offsetHeight-this.$refs.mov.offsetHeight){
      y=this.$refs.gun.offsetHeight-this.$refs.mov.offsetHeight;
    }
    this.positionLT = y;
    this.Init();
}
function mouseup(e){
    document.removeEventListener('mousemove',this.bindMousemove,false);
    document.removeEventListener('mouseup',this.bindMouseup,false);
}
import Collipse from "./Collipse.vue";
export default {
  name: "HelloWorld",
  components: { Collipse },
  props:['sj'],
  data() {
    return {
      isShow:false,
      positionLT:0,
      moveTo:0,
      scrollY:0,
      long:0,
      flag:'',
      bkd:''
    };
  },
    created(){
        this.bindMousemove = mousemove.bind(this);
        this.bindMouseup = mouseup.bind(this);
    },
  methods:{
    handchange(val,index){
        this.$nextTick(()=>{  
        if(this.$refs.big.offsetHeight<this.$refs.wrap.offsetHeight){
          this.isShow = true;
          let moveTop = this.$refs.big.offsetHeight / this.$refs.wrap.offsetHeight * 200;
          this.moveTo = parseInt(moveTop);
        }else if(this.$refs.big.offsetHeight>this.$refs.wrap.offsetHeight){
          this.isShow= false;
          this.positionLT = 0;
        }
        if(this.sj[index].cout){
         this.flag = this.sj[index].cout[0].name;
         this.watch.emit('post',this.flag);
         this.watch.emit('postpath',this.sj[index].cout[0].path);
        }
        this.bkd =  this.sj[index].title;
        })
    },
    down(ev){
      this.my = ev.clientY-ev.target.offsetTop;
      document.addEventListener('mousemove',this.bindMousemove,false);
      document.addEventListener('mouseup',this.bindMouseup,false);
    },
       mouseEnter(){
          window.onmousewheel = this.isShow?(ev)=>{
          let offsetY = this.positionLT + ev.deltaY / 10;
          let maxTop = this.$refs.gun.offsetHeight - this.moveTo;
          if(offsetY < 0){
              offsetY = 0;
          }
          if(offsetY > maxTop){
              offsetY = maxTop;
          }
          this.positionLT  = offsetY;
          this.Init();
        }:null;
        },
        mouseLeave(){
            window.onmousewheel = null;
        },
        Init(){
           this.scrollY = (this.$refs.gun.offsetHeight-this.$refs.mov.offsetHeight) / (this.$refs.wrap.offsetHeight - this.$refs.big.offsetHeight);
        },
        add(val,path){
          this.flag = val;
          this.watch.emit('post',val);
          this.watch.emit('postpath',path);
        }
  }, 
  mounted(){ 
  this.Init();
  this.bkd = this.sj[0].title;
  },
  computed:{
    siderY(){
    return -this.positionLT / this.scrollY;
    }
  }

};
</script>

<style scoped>
.hello{
  width: 200px;
  height: 100%;
  background: #2F3B4C;
  color:#fff;
  overflow: hidden;
  position: relative;
}
a{
  color: #ccc;
}
.wrapper{
  width: 190px;
  height: auto;
  position: absolute;
  left: 0;
}
.scrolly{
  width: 15px;
  height: 100%;
  background: rgba(195, 190, 255, 1);
  position: absolute;
  right: 0;
  top: 0;
}
.scrolly .move{
  width: 100%; 
  background: black;
  cursor: pointer;
  position: absolute;
  left: 0;
}
.small{
  cursor: pointer;
}
.active  a{
  color: #fff;
}
.choose a{
  width: 100%;
  height: 100%;
  display: inline-block;
  background: #229FFF;
  color: #fff;
}
.hd{
  width: 100%;
  height: 100%;
}
</style>


