export default {
    addmsg(context, payload) {
        let { commit } = context;
        let getMsgList = localStorage.getItem('add');
        getMsgList = getMsgList ? JSON.parse(getMsgList) : [];
        getMsgList.push(payload.msg);
        let newArr = Array.from(new Set(getMsgList));
        localStorage.setItem('add', JSON.stringify(newArr));
        commit({ type: 'add', newArr });
    }
}


