<script>
export default {
    methods:{
        headClick(){
            this.collipse.isShow = !this.collipse.isShow;
            this.$emit('handleCollipse',this.collipse.isShow,this.index);
        }
    },
    props:{
        collipse:{
            type:Object,
            default(){
                return {
                    isShow:false,

                }
            }
        },
        index:{
            type:Number
        }
    },
    mounted(){
    
    }
}
</script>
<template>
    <div class="collipse-wrap" ref="h">
         <div class="collipse-head" @click="headClick" >
            <slot name="head"></slot><span v-if="index!==0" class="sp">{{!collipse.isShow?'v':'^'}}</span>
        </div>
        <div class="collipse-body" v-show="collipse.isShow">
            <slot name="list"></slot>
        </div>
    </div>
</template>
<style>
.collipse-wrap{
    color: #bbb;
  padding: 10px 10px;
} 
.collipse-head{
    width: 100%;
    height: 30px;
    display: flex;
    cursor: pointer;
    line-height: 30px;
}
.collipse-body{
  line-height: 38px;
}
.sp{
    position: absolute;
    right: 15px;
}
</style>





