export default {
    sanchu(state, index) {
        state.count.splice(index, 1);
        localStorage.setItem('add', JSON.stringify(state.count));
    },
    add(state, arr) {
        state.count = arr.newArr;
    },
    change(state, arr) {
        state.count = arr;
    }
}


