<template>
    <div>
      <input type="text" placeholder="请输入添加的值" v-model="val"><button @click="add">添加</button>
      <div>
        <p v-for="(item,index) in count" :key="index" >
          <span>{{item}}</span> <button @click="delet(index)" v-show="item">删除</button>
        </p>
      </div>
      <div v-show="count.length>0">您一共添加了{{count.length}}条内容</div>
    </div>
</template>

<script>
import { mapState ,mapActions,mapMutations} from 'vuex'
export default { 
  data(){
    return{
      val:''
    }
  },
   methods:{
     ...mapActions(['addmsg']),
     ...mapMutations(['change','sanchu']),
       add(){
         if(!this.val==''){
           this.addmsg({msg:this.val});
         }else{
           alert('您输入的内容不能为空');
         }
         this.val = '';
       },
       delet(index){
          this.sanchu(index);   
       }
   },
   computed:{
    ...mapState(['count'])
   },
    mounted(){
       let getMsgList = localStorage.getItem('add');
           getMsgList = JSON.parse(getMsgList);
           this.change(getMsgList); 
          //  console.log(this.$store);
    }
}
</script>

<style>

</style>



