<template>
    <div class="top">
      <div v-for="(item,index) in list" :key="index"  :class="flag==item?'active dj':'dj'" @click="act(item)">
        <router-link :to="{path:url[index]}"><p>{{item}}</p></router-link><span :class="item==flag?'close act':'close'" @click="close(index,$event,item)">x</span>
      </div>  
    </div>
</template>

<script>
export default {
    data(){
      return{
       list:[],
       flag:'',
       url:[]
      }
    },
  mounted(){
     this.watch.on('post',(val)=>{
     this.flag =val;
     this.list.push(val.toString());
     let newArr =Array.from(new Set(this.list));
     this.list = newArr;
     this.watch.emit('arr',...this.list);
     })
     this.watch.on('postpath',(val)=>{
     this.url.push(val.toString());
     let newArr =Array.from(new Set(this.url));
     this.url = newArr;
    }); 
  },
  methods:{
      close(ind,e,val){
       e.stopPropagation();
       if(val==this.flag){   
       this.$nextTick(()=>{
        this.flag=this.list[this.list.length-1]; 
        })
        }
        this.list.splice(ind,1); 
        this.watch.emit('arr',...this.list);
        if(this.url[ind-1]){
          this.$router.push(this.url[ind-1]);
        }else{
            this.$router.push('/Home/first');
        }
        },
      act(item){
          this.flag=item;
      }
  }
}
</script>

<style scoped>
 .top{
     width: 1160px;
     height: 50px;
     border-bottom: 1px solid #bbb;
     display: flex;
     align-items: center;
 }
 .top .dj{
     padding: 12px 20px;
     border-bottom: 3px solid #bbb;
     position: relative;
     color: #bbb;
     margin-left: 15px;
     cursor: pointer;
 }
 .dj .close{
     position: absolute;
     right: 1px;
     top: 16px;
     width: 15px;
     height: 15px;
     border-radius: 50px;
     color: #fff;
     background: #bbb;
     line-height: 14px;
     text-align: center;
     cursor: pointer;
 }
 .top .active{
     color: blue;
     border-bottom: 3px solid blue;
 }
 .dj .act{
     background: blue;
 }
</style>



