<template>
    <div class="box">
        <div class="logo_div">
        <div class="logo">
            <p class="big">赚</p>
            <p>赚赚金融渠道管理系统</p>
        </div>
        <div class="info">
          <ul>
            <li>
              <input type="text" placeholder="注册邮箱/手机号">
            </li>
            <li>
              <input type="password" placeholder="登陆密码">
            </li>
            <li>
              <input type="text" placeholder="验证码">
              <button>获取验证码</button>
            </li>
          </ul>
        </div>
        <div class="forget">
          忘记密码？
        </div>
        <button class="btn_logo" @click="login">
        登陆
        </button>
      </div>  
    </div>
</template>

<script>
export default {
  methods:{
    login(){
      this.$router.push('/Home/first');
    }
  }
}
</script>

<style scoped>
.box{
    width: 100%;
    height: 100%;
    background: url('/static/img/login.png') no-repeat center center;
    background-size: 100% 100%;
    position: relative;
}
.box .logo_div{
    width: 30%;
    height: 45%;
    position: absolute;
    right: 13%;
    bottom: 33%;
    text-align: center;
}
.logo .big{
    width: 45px;
    height: 45px;
    background: purple;
    color: #fff;
    text-align: center;
    line-height: 50px;
    font-weight: bold;
    font-size: 30px;
    display: inline-block;
    border-radius: 10px;
    margin-top: 20px;
}
.logo{
  text-align: center;
  margin-top: 20px;
}
.info{
  width: 80%;
  margin: 15px auto;
}
.info ul li{
  border-bottom: 1px solid #ccc;
  line-height: 2.4
}
.info ul li input{
  border: none;
  padding-left: 10px;
}
.info ul li button {
  background: #fff;
  border: 1px solid #ccc;
  padding: 4px 12px;
}
.forget {
  width: 80%;
  margin: 0 auto;
  text-align: right;
  font-size: 12px;
  color: #444;
}
.btn_logo {
  display: block;
  width: 80%;
  margin: 20px auto;
  border-radius: 20px;
  line-height: 2.6;
  background: #7b1ad9;
  color: #fff;
  border: none
}


</style>



