<template>
    <div class="box">
     <div class="left">
         <div class="scrol">
         <HelloWorld :sj='sj'/>     
         </div>    
     </div>
     <div class="right">
      <Top  v-show="flag.length>0" />
       <router-view/>   
     </div>   
    </div>
</template>

<script>
import  HelloWorld from '../components/HelloWorld.vue';
import Top from '../components/top.vue';
import data from  '../../static/data/data.js';
export default {    
 components:{HelloWorld,Top},
 data(){
     return{
       flag:[],
       sj:data
     }
 },
 mounted(){
     this.watch.on('arr',(val)=>{
        this.flag=val;
     })
 }
}
</script>

<style scoped>
 .box{
     width: 100%;
     height: 100%;
     display: flex;
 }
 .box .left{
     height: 100%;
 }
 .box .scrol{
     height: 80%;
 }
</style>



