import Vue from 'vue'
import Vuex, { Store } from 'vuex'
import getters from './getters'
import actions from './actions'
import mutations from './mustions'
Vue.use(Vuex)
const store = new Store({
    state: {
        count: []
    },
    getters,
    mutations,
    actions
});
export default store;


